@keyframes letter-spacing {
  0% {
    letter-spacing: normal;
  }
  5% {
    letter-spacing: 20;
  }
  100% {
    letter-spacing: normal;
  }
}
@keyframes letter-name {
  0% {
    letter-spacing: normal;
  }
  5% {
    letter-spacing: 40;
  }
  100% {
    letter-spacing: normal;
  }
}

.products {
  padding-bottom: 130px;
}

.cards {
  margin-top: 60px;
}
.plus-section {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  margin-bottom: 15px;
  color: $secondary-color;
  &:hover {
    animation-name: letter-spacing;
    animation-duration: 2s;
  }
}

.name-section {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 34px;
  line-height: 39px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $primary-color;
  &:hover {
    animation-name: letter-name;
    animation-duration: 4s;
  }
}
.section-title {
  padding-bottom: 200px;
}
.list_card {
  @extend .list;
  width: 280px;
  //height: 489px;//
  flex: row;
  row-gap: 200px;
}

.products__card-list {
  position: relative;
}
.product--name {
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffff;
}
.product--description {
  text-align: center;
  letter-spacing: 0.04em;
  color: #fff;
  padding: 20%;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 152%;
}
.list-description:not(:last-child) {
  margin-bottom: 32px;
}
.image_product {
  position: relative;
}
.image {
  position: absolute;
  right: -23px;
  top: -200px;
}
.point {
  color: #fff;
  justify-content: center;
  font-size: 50px;
  display: flex;
}
.img-pink {
  background-color: $secondary-color;
  border-radius: 24px;
  //margin-bottom: 200px;//
  padding-top: 150px;
  display: flex;
  flex-direction: column;
  bottom: -200px;
}
.img-brown {
  @extend .img-pink;
  background-color: $fifth-color;
}
.img-green {
  @extend .img-pink;
  background-color: $fourth-color;
  bottom: 0px;
}
.btn {
  position: relative;
  left: 116px;
  fill: $primary-color;
  border: 2px;
  border-radius: 100%;
  margin-bottom: 40px;
  background-color: #ffff;
  width: 40px;
  height: 40px;
}
.list_card {
  justify-content: flex-start;
  display: flex;
  bottom: 300px;
}
.products-description:hover .btn {
  transform: rotate(-90deg);
  width: 50px;
  height: 50px;
  margin-top: -10px;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.15);
  background-color: $primary-color;
  border-radius: 35%;
  fill: #ffff;
  cursor: pointer;
  transition-duration: 500ms;
}
.products-description:hover .list-description {
  cursor: pointer;
  opacity: 1;
  box-shadow: 0px 5px 15px rgba(144, 35, 62, 0.632);
  border-radius: 24px;
  transition-duration: 500ms;
}

@media screen and (min-width: 768px) {
  .product {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .plus-section {
    font-size: 26px;
    padding-bottom: 18px;
  }
  .name-section {
    font-size: 48px;
    margin-bottom: 80px;
  }
  .section-title {
    display: flex;
    flex-direction: column;
    padding-bottom: 70px;
  }
  .list_card {
    display: flex;
    flex-wrap: wrap;
    width: 224px;
    height: 473px;
    gap: 16px;
  }
  .image {
    width: 220px;
    height: 224px;
    right: 0px;
    top: -120px;
  }
  .btn {
    left: 90px;
  }
}
@media screen and (min-width: 1200px) {
  .list_card {
    margin: 0px;
  }
  .section-title {
    margin-bottom: 100px;
    display: flex;
    justify-content: center;
    padding-bottom: 150px;
  }
  .plus-section {
    font-size: 30px;
    padding-bottom: 16px;
  }
  .name-section {
    font-size: 58px;
  }
  .image {
    width: 368px;
    height: 394px;
    right: 0px;
    top: -250px;
  }
  .img-pink:not(:last-child):not(:last-child) {
    margin-right: 32px;
  }
  .btn {
    left: 160px;
  }
  .product--name {
    font-size: 30px;
    line-height: 34.35px;
    font-family: 'Titan One';
    font-weight: 400px;
  }
  .product--description {
    font-size: 16px;
    font-family: 'DDM Sans';
    font-weight: 700;
    line-height: 28px;
  }
  .list_card {
    width: 368px;
    height: 535px;
    justify-content: center;
    display: flex;
  }
}
